<template>
    <div>
        <top-nav title="顾问申请详情"></top-nav>
        <div class="wyu-main" v-loading="loading">
            <div class="wyu-block">
                <div class="wyu-tips wyu-tips-info" style="margin-bottom:10px;">
                    <p> 1、顾问审核可以是拒绝还是同意 </p>
                    <p>2、同意要设置同意后的顾问分组及提成率；</p>
                    <p>3、不同意要填写拒绝理由；</p>
                </div>

                <el-form  v-if="detail!=null"  label-width="100px" style="padding-bottom: 0">

                    <el-form-item label="基本信息">
                        <img style="float:left;" width="100" height="100" :src="detail.face" />
                        <div class="userinfo-info" >
                            <p>手机：{{detail.mobile}}</p>
                            <p>昵称：{{detail.nickname}}</p>
                            <p>注册：{{detail.reg_time}}</p>
                            <p>最后登录{{detail.last_time}}</p>
                        </div>
                    </el-form-item>

                    <el-form-item label="会员卡情况">
                        <el-tag v-if="detail.vip == 0" type="info">非会员</el-tag>
                        <el-tag v-if="detail.vip == 1" type="message">PLUS会员</el-tag>
                        <span v-if="detail.vip == 1">有效期 {{detail.vip_time}}</span>
                    </el-form-item>

                    <el-form-item label="订单数">
                        {{detail.order_num}} 个
                    </el-form-item>

                    <el-form-item label="推广情况">
                        <el-tag v-if="detail.vip == 0" type="info">{{detail.agent_group_mean}}</el-tag>
                        <el-tag v-if="detail.vip == 1">{{detail.agent_group_mean}}</el-tag>
                        <el-tag v-if="detail.vip == 2" type="success">{{detail.agent_group_mean}}</el-tag>
                        <el-tag v-if="detail.vip == 3" type="danger">{{detail.agent_group_mean}}</el-tag>
                        提成率：{{detail.agent_rate}}%; 子推广员:{{detail.agent_num}} 个(直接下级); 推广注册用户：{{detail.agent_register}} 个;
                        推广VIP：{{detail.agent_vip}} 个; 推广订单:{{detail.agent_order}}个
                    </el-form-item>

                    <el-form-item label="申请时间">
                        {{detail.add_time}}
                    </el-form-item>
                </el-form>

                <el-form  v-if="detail!=null"   label-width="100px" >
                    <el-form-item label="审核状态">
                        <el-radio-group v-model="save.status" size="small">
                            <el-radio :label="1" border>同意</el-radio>
                            <el-radio :label="2" border>拒绝</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item v-if="save.status==1" label="选择分组">
                        <el-select size="small" v-model="save.group" placeholder="请选择">
                            <el-option label="请选择"        :value="0"></el-option>
                            <el-option label="普通顾问"      :value="1"></el-option>
                            <el-option label="资深顾问"      :value="2"></el-option>
                            <el-option label="专家顾问"      :value="3"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item  v-if="save.status==2" label="拒绝理由">
                        <el-input style="width:500px;" type="textarea" :rows="3" placeholder="请输入内容" v-model="save.reason"></el-input>
                    </el-form-item>
                    <div class="miniapp-btn">
                        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                        <el-button type="primary" size="small" @click="saveForm" :loading="loading2">确定审核</el-button>
                    </div>
                </el-form>


            </div>
        </div>
    </div>
</template>
<script>
    import * as api from '@/api.js';
    import TopNav from "@/components/TopNav";

    export default {
        name: "hotelDepart",
        components: {TopNav},
        data: function() {
            return {
                detail:null,
                loading:false,
                nodata:false,
                loading2:false,
                save:{
                    status:0,
                    group:0,
                    reason:'',
                },
            };
        },
        created() {
            this.getDetail();
        },
        methods: {
            saveForm(){
                var that = this;
                that.loading = true;
                api.post('shop/admin.agent.agentapply/save',{
                    appToken: this.$store.getters.appToken,
                    apply_id:that.$route.params.id,
                    status:that.save.status,
                    group:that.save.group,
                    reason:that.save.reason
                }).then(res=>{
                    that.loading = false;
                    that.$message({
                        message:'审核成功',
                        type:'success',
                    });
                    that.$router.replace('/agent/apply/index');
                }).catch((error) => {
                    that.loading = false;
                    that.nodata = true;
                });
            },
            getDetail(){
                var  that = this;
                that.loading = true;
                api.post('shop/admin.agent.agentapply/view',{
                    appToken: this.$store.getters.appToken,
                    apply_id:that.$route.params.id,
                }).then(res=>{
                    that.loading = false;
                    that.detail = res.detail;
                }).catch((error) => {
                    that.loading = false;
                    that.nodata = true;
                });
            },

        }
    };
</script>

<style>
    .userinfo-info {
        line-height: 26px;
        float: left;
        margin-left: 10px;
    }

</style>
